/* Загальні стилі для сторінки */
.body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
  }

  .dis_txt{
    position: relative;
    font-size: 32px;
    font-family: Roboto;
    color: #0c0c0c;
    text-align: left;
    padding-bottom: 28px;
  }
  
  /* Контейнер для списку продуктів */
  .discount-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 3px;  
    justify-items: center;   
  }
  
  /* Стилі для кожного продукту */
  .product { 
    display: flex;
    flex-direction: column; /* Вертикальне розташування елементів */
    justify-content: space-between; /* Розміщуємо елементи рівномірно */ 
    position: relative;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    width: 100%; 
    max-width: 300px;
    text-align: center;
    transition: transform 0.2s;  
    cursor: pointer;
  }

  .product:hover {
    transform: translateY(-5px);
  }
  
  
  .product_image_container {
    position: relative; 
    width: 100%; /* Повний розмір контейнера для фотографії */
    padding-top: 75%; /* Забезпечуємо 4:3 аспект відносно ширини контейнера */
    overflow: hidden; 
    display: flex; /* Встановлюємо flex контейнер */
    align-items: center; /* Вертикально вирівнюємо фотографію по центру */
    justify-content: center; /* Горизонтально вирівнюємо фотографію по центру */
    margin-top: 10px; 
  }

  .dis_product-img{
    position: absolute; 
    top: 0;  
    max-width: 100%; /* Розтягуємо фотографію на весь контейнер */
    max-height: 100%; /* Розтягуємо фотографію на весь контейнер */ 
    transition: transform 0.2s;  
  }


  .product_content_ { 
    padding-bottom: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%; /* Встановлюємо висоту для вирівнювання елементів */
  }
  
  .product h2 {
    font-size: 1.2rem;
    color: #333;
    margin: 10px 0;
  }
  
  .product p {
    font-size: 1rem;
    color: #666;
    margin: 10px 0;
}

.discount-tag {
  position: absolute;
  top: 0px;  
  right: 0px;  
  background-color: #ebba1a;
  border-radius: 8px;
  color: #0c0c0c;
  padding: 8px;
  font-size: 18px;
}

.original-price { 
  font-size: 1rem;
  color: #666;
  margin-bottom: 10px;
  font-family: Roboto;
  text-decoration: line-through;
  text-align: left; /* Додаємо вирівнювання вліво */ 
  padding-left: 10px; 
  font-size: 14px;
  font-weight: 300px;
}

.discounted-price { 
  font-size: 1rem;
  color: #e60000;
  margin-bottom: 10px;
  font-size: 20px;
  font-family: Roboto; 
  text-align: left; /* Додаємо вирівнювання вліво */ 
  padding-left: 10px; 
  font-weight: 500; 
}
 
 
.icon-heart {   
  width: 20px; /* Фіксована ширина для сердечка */
    height: 20px; /* Фіксована висота для сердечка */
    position: relative;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}

.availability {
  display: flex;
  align-items: center; /* Вертикальне вирівнювання по центру */
  justify-content: space-between; /* Простір між елементами */
  height: 18px;
  font-size: 14px;
  color: #169b00;
  font-family: Roboto;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
}



.bask { 
  font-weight: 500;   
  }

  .button-basket {  
  position: relative;
  border-radius: 8px;
  background-color: #091d9e;
  height: 40px;
  width: 178px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; 
  box-sizing: border-box;
  text-align: center;
  font-size: 14px;
  color: #f6f6f6;
  font-family: Roboto;
    margin: 0 auto; /* Вирівнювання по центру */
    margin-bottom: 10px;
  }